.add-btn {
	height: 40px;
	line-height: 40px;
	max-width: 115px;
	& * {
		vertical-align: middle;
	}
	input {
		height: 100%;
	}
}

.round-btn {
	width: 40px !important;
	height: 40px !important;
	box-shadow: unset;
	border-radius: 50% !important;
	min-width: unset !important;

	.mat-icon {
		margin-inline-start: auto !important;
		margin-inline-end: auto !important;
	}
}

.input-width {
	max-width: 30px;
}
